.ProfileMain {
  padding: 20px;
  background-color: #f7f7f7;
}
.ProfileMain h2 {
  font-weight: 500;
  font-size: 30px;
  color: #101828;
  margin: 0px;
}
.ProfileMain .nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link {
  margin-top: 20px;
}
.BasicDivMain {
  padding: 10px 20px !important;
  background-color: #f7f7f7;
}
.ProfileMain .active {
  background-color: transparent !important;
}
.BasicDivMain .profile-image {
  width: 138px;
  height: 138px;
  border-radius: 50%;
}
.Camera {
  position: absolute;
  left: 460px;
  top: 250px;
  height: 28.34px;
  width: 28.34px;
  border-radius: 50%;
  background-color: #101828;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #ababab;
}
.Camera svg {
  color: #fff;
}
.Camera label {
  display: flex;
  align-items: center;
  justify-content: center;
}
.ProfileInputs {
  padding: 20px;
  width: 80%;
}

.ProfileChild {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ProfileLabels {
  width: 30%;
}
.ProfileLabels label {
  color: #344054;
  font-weight: 500;
  font-size: 14px;
}
.ProfileChild input {
  width: 70%;
  border-radius: 8px;
  padding: 10px;
  outline: none;
  border: 1px solid #d0d5dd !important;
}
.MailProfile {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: end;
  border: 1px solid #d0d5dd;
  padding: 0px 10px;
  border-radius: 8px;
  background-color: #fff;
}
.MailProfile input {
  outline: none;
  border: none !important;
  width: 100%;
  background-color: transparent;
}
.MailProfile svg {
  color: #667085;
}
.ProfileArea {
  width: 77%;
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
  margin-top: 20px;
}
.AreaInput {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.AreaInput span {
  color: #ff0000;
}
.AreaInput input {
  border-radius: 8px;
  border: none;
  padding: 10px;
  outline: none;
  color: #667085;
  font-weight: 400;
  font-size: 16px;
}
.ProfileBtns {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
  margin-top: 20px;
}
.ProfileBtns button {
  height: 40px;
  padding: 0px 30px;
  font-weight: 600;
  font-size: 14px;
  border-radius: 8px;
  box-shadow: 0px 1px 2px 0px #1018280d;
}
.ProfileBtns .ProfileCancel {
  border: 1px solid #101828;
  color: #101828;
  background-color: #f7f7f7;
}
.ProfileBtns .ProfileSave {
  color: #fff;
  background-color: #101828;
  border: none;
}
.PaymentProfile {
  display: flex;
  align-items: center;
  background-color: #fff;
  gap: 10px;
  border-radius: 12px;
  padding: 10px 20px;
  width: 60%;
  margin-top: 10px;
}
.BasicDivMain h2 {
  font-weight: 500;
  font-size: 14px;
  color: #101828;
}
.PaymentProfile p {
  font-weight: 400;
  font-size: 14px;
  color: #101828;
  margin-bottom: 0px;
}
.PaymentProfile svg {
  font-size: 20px;
}
.PaymentMehhods {
  margin-top: 40px;
}
.PaymentProfile img {
  width: 24px;
  height: 21px;
}
.ForWidth {
  width: 60%;
}
.ProfileChild .PhoneInput {
  width: 70%;
}
.FlexEnd {
  width: 59.5% !important;
  display: flex !important;
  align-items: center !important;
  justify-content: end !important;
}
@media (max-width: 1495px) {
  .ProfileArea {
    width: 100%;
  }
  .Camera {
    left: 394px;
  }
}
@media (max-width: 1395px) {
  .Camera {
    left: 362px;
  }
}
@media (max-width: 1151px) {
  .Camera {
    left: 336px;
  }
}
@media (max-width: 1039px) {
  .Camera {
    left: 318px;
  }
  .ProfileInputs {
    width: 100%;
  }
}
@media (max-width: 893px) {
  .Camera {
    left: 138px;
    top: 289px;
  }
  .ProfileMain h2 {
    font-size: 26px;
    margin-top: 40px;
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .Camera {
    left: 138px;
    top: 270px;
  }
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    font-size: 14px;
  }
  .PaymentProfile {
    width: 100%;
  }
  .PaymentMehhods {
    margin-top: 20px;
  }
  .ProfileMain h3 {
    font-size: 20px;
  }
  .AreaInput input {
    padding: 7px;
  }
  .ProfileLabels label {
    font-size: 12px;
  }
  .ProfileChild input {
    padding: 7px;
  }
  .AreaInput label {
    font-size: 12px;
  }
}
@media (max-width: 643px) {
  .ProfileInputs {
    padding: 20px 0px;
  }
  .BasicDivMain {
    padding: 0px;
  }
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    font-size: 12px;
  }
  .ProfileMain h2 {
    font-size: 22px;
  }
  .AreaInput input {
    width: 100%;
  }
  .AreaInput {
    width: 30%;
  }
  .Camera {
    left: 134px;
    top: 242px;
  }
}
@media (max-width: 565px) {
  .nav-link {
    padding: 5px !important;
  }
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    font-size: 11px;
  }
  .ProfileMain h2 {
    font-size: 20px;
  }
  .ProfileLabels label {
    font-size: 10px;
  }
}
@media (max-width: 454px) {
  .ProfileBtns {
    margin-top: 12px;
  }
  .ProfileBtns button {
    height: 32px;
    padding: 0px 14px;
    font-size: 11px;
  }
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    font-size: 10px;
  }
}
@media (max-width: 411px) {
  .Camera {
    left: 134px;
    top: 255px;
  }
  .ProfileMain h3 {
    font-size: 16px;
  }
  .PaymentProfile {
    padding: 10px;
  }
  .PaymentProfile p {
    font-size: 12px;
  }
  .ProfileBtns {
    justify-content: center !important;
  }
  .ForWidth {
    width: 100%;
  }
  .ProfileChild input {
    padding: 7px;
    font-size: 11px;
  }
  .AreaInput input {
    font-size: 11px;
  }
}
@media (max-width: 380px) {
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    font-size: 10px !important;
  }
  .ProfileMain {
    padding: 20px 10px;
    background-color: #f7f7f7;
  }

  .ProfileMain h2 {
    margin-top: 32px;
  }
  .Camera {
    left: 124px;
  }
}
