.Settings {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f7f7f7;
}
.Settings .mainSettings {
  width: 98%;
}
.Settings .mainSettings h2 {
  color: #101828;
  font-size: 30px;
  font-weight: 500;
  margin: 28px 0px;
}

.Settings .mainSettings .tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  width: 100%;
  border-bottom: 1px solid #d7d7d7;
}

