  padding: 8.5px 20px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  color: #667085;
  font-size: 14px;
  font-weight: 500;
}

.Settings .mainSettings .tabButton.active {
  color: #000000;
  border-bottom: 1px solid #205ad0;
}

.Settings .mainSettings .tabContent {
  width: 100%;
}
