.Notifications .notificationCards h1 {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 1px solid #101828;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px;
  font-size: 10px;
}

.notificationCards .modals {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.notificationCards .modalContent {
  position: relative;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  text-align: center;
  border-radius: 44px;
  width: 648px;
}
.notificationCards .modalContent span {
  position: absolute;
  right: -5px;
  top: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.notificationCards .modalContent img {
  width: 40%;
  margin-bottom: 2rem;
}

.notificationCards .modalContent h4 {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
  color: #212121;
}
.notificationCards .modalContent p {
  color: #212121;
  font-size: 16px;
  font-weight: 400;
}
.notificationCards .modalContent .btns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
}

.notificationCards .modalContent .btns button {
  width: 47%;
  height: 58px;
  border-radius: 16px;
  color: 16px;
  font-weight: 700;
  background-color: #1018281a;
  color: #101828;
  font-family: Plus Jakarta Sans;
  border: none;
}
.notificationCards .modalContent .btns .yesBtn {
  background-color: #101828;
  color: #ffffff;
}
