.adminpenal-main {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #e7e7e7;
  font-family: "Poppins", sans-serif;
}

.saidbar-main {
  width: 20%;
  height: 100vh !important;
  padding: 0px 10px;
  background-color: #101828;
  position: relative;
}

.saidbar-main .logo {
  margin: 20px 0px 20px 0px;
  height: 56px;
}

.saidbar-main .menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.saidbar-main .menu ul {
  margin: 0px;
  padding: 0px;
}
.saidbar-main .LinkLogo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  font-size: 24px;
  color: #ffffff;
  text-decoration: none;
}
.saidbar-main .menu ul li {
  margin: 15px 0px 0px 0px;
  list-style: none;
}

.saidbar-main .menu ul li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 10px !important;
  border-radius: 8px;
}

.saidbar-main .menu ul li .counter {
  font-size: 9px;
  /* color: #fff; */
  color: white;
  padding: 2px 6px 2px 6px;
  border-radius: 50%;
  margin-top: -20px;
  margin-left: 10px;
  background-color: #007dfa;
  display: flex;
  align-items: center;
  justify-content: center;
}

.saidbar-main .menu ul li a svg {
  width: 24px;
  height: 24px;
  margin-right: 26px;
}

.saidbar-main .menu ul li a:hover {
  background-color: #f2f2f5;
  color: #282d4f;
}

.SidebArImg {
  height: 20px;
  margin-right: 10px;
}

.saidbar-main .menu ul li .active {
  color: #101828;
  background-color: #ffffff;
}

.saidbar-main .menu .hamburger {
  font-size: 2.2rem;
  display: none;
}

.saidbar-main .setting {
  display: flex;
  gap: 10px;
  width: 100%;
  align-items: center;
  padding: 20px 0px !important;
  position: absolute;
  bottom: 0;
}

.saidbar-main .setting .rowww {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding: 0px !important;
}

.saidbar-main .setting .rowww img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 0px;
  padding: 0px;
}

.saidbar-main .setting i {
  color: red;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
}
.admin-name {
  width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.saidbar-main .setting .rowww .names a {
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  margin: 0px;
  text-decoration: none;
}
.saidbar-main .setting .rowww .names p {
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  margin: 0px;
  padding: 0px;
}
.saidbar-main .setting i svg {
  cursor: pointer;
}

.adminpenal-main .right-said {
  width: 100%;
  display: flex;
  background-color: #f2f2f5;
  flex-direction: column;
}

.search-top {
  width: 100% !important;
  display: flex;
  border-bottom: 1px solid #dddddd;
  position: relative;
}

.search-top .search {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0px 0rem 0px 2rem;
}

.search-top .search button,
.search-top .search input {
  border: none;
  background-color: transparent;
  outline: none;
}

.search-top .search button {
  font-size: 1.5rem;
  color: #b8bed7;
  margin: 0px 10px 0px 10px;
}

.search-top .search input {
  font-size: 16px;
  padding: 20px 5px;
  width: 100%;
}

.search-top .search input:focus ~ .adminpenal-main .search-top {
  background-color: #fff;
}

.search-top span {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 2rem;
  padding-left: 2rem;
  color: #282d4f;
  position: relative;
  border-left: 1px solid #dbdada;
  cursor: pointer;
}

.search-top span i {
  position: absolute;
  top: 5px;
  right: -1rem;
  background-color: #0c8ce9;
  color: #fff;
  width: 20px;
  height: 20px;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-style: normal;
}

.search-top img {
  height: 24px;
  width: 24px;
}

.adminpenal-main .search-user {
  width: 100%;
  background-color: #f2f2f5;
  padding-bottom: 2rem;
}

.showSearch {
  width: 100%;
}

.adminpenal-main .right-said .dashboard {
  width: 95%;
  margin: 1rem 0px 0px 2.5rem;
}

.adminpenal-main .right-said .dashboard .topbox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-block: 15px;
}

.adminpenal-main .right-said .dashboard .topbox h2 {
  margin: 0px;
  color: #282d4f;
  font-size: 32px;
  font-weight: 700;
}

.adminpenal-main .right-said .dashboard .topbox select {
  border-color: #dedede;
  background-color: transparent;
  border-radius: 8.5px;
  padding: 3.5px 1px;
  outline: none;
  color: #1c1c1c;
  font-weight: 500;
  font-size: 14.7px;
}

.adminpenal-main .right-said .dashboard .selectbox {
  display: flex;
  gap: 5px;
  justify-content: space-between;
  align-items: center;
}

.adminpenal-main .right-said .dashboard .selectbox .box {
  width: 23%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 130px;
  background-color: #7357f614;
  border-radius: 18.29px;
  padding: 25px;
}

.adminpenal-main .right-said .dashboard .selectbox .box p {
  color: #1c1c1c;
  font-size: 16px;
  font-weight: 500;
}

.adminpenal-main .right-said .dashboard .selectbox .box .price {
  display: flex;
  align-self: center;
  width: 100%;
  justify-content: space-between;
}

.adminpenal-main .right-said .dashboard .selectbox .box .price .profit {
  font-size: 27.43px;
  font-weight: 700;
  margin: 0px;
  color: #1c1c1c;
}

.adminpenal-main .right-said .dashboard .selectbox .box .price .plas {
  font-size: 18px;
  font-weight: 450;
  margin: 0px;
  color: #0e9a0c;
}

.adminpenal-main .right-said .dashboard .selectbox .box2,
.adminpenal-main .right-said .dashboard .selectbox .box4 {
  background-color: #d8e5f4;
}

.adminpenal-main .right-said .dashboard .selectbox .box3 {
  background-color: #f2e4cb;
}

.adminpenal-main .right-said .dashboard .selectbox .box4 .price .plas,
.adminpenal-main .right-said .dashboard .selectbox .box3 .price .plas {
  color: #f23939;
}

.adminpenal-main .right-said .dashboard .selectchart {
  width: 100%;
}

.adminpenal-main .right-said .dashboard .selectchart .chart {
  width: 100%;
  padding: 25px;
  border-radius: 16.8px;
  background-color: #fff;
  margin-bottom: 1rem;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance h3 {
  font-size: 14px;
  font-weight: 700;
  color: #1c1c1c;
  margin: 0px;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance .Income {
  display: flex;
  align-items: center;
  gap: 10px;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance .Income p,
.adminpenal-main .right-said .dashboard .selectchart .chart .Balance .Income h6,
.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h5 {
  display: flex;
  align-items: center;
  margin: 0px;
  gap: 5px;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance .Income p {
  font-size: 14px;
  font-weight: 400;
  color: #8e8ea1;
}

.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  p
  svg {
  color: #0c8ce9;
  height: 12px;
  width: 12px;
}

.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h1 {
  font-size: 18px;
  font-weight: 600;
  color: #22242c;
  margin-bottom: 0px;
}

.adminpenal-main .right-said .dashboard .selectchart .chart .Balance .Income h6,
.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h5 {
  color: #1c1c1c;
  font-size: 12.6px;
  font-weight: 450;
}

.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h6
  svg,
.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h5
  svg {
  color: #7357f6;
  width: 10px;
  height: 10px;
}

.adminpenal-main
  .right-said
  .dashboard
  .selectchart
  .chart
  .Balance
  .Income
  h5
  svg {
  color: #ed7e63;
}

.adminpenal-main .right-said .dashboard .selectchart .chart canvas {
  width: 100% !important;
}

.active {
  background-color: #f2f2f5;
  color: #282d4f;
}

.hamburger {
  display: none;
}
.LogoutLastModal {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.LogOutIMG {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.LogOutIMG h3 {
  font-weight: 700;
  font-size: 23.8px;
  color: #363b47;
  margin-bottom: 0px;
}
.LogOutIMG img {
  height: 25px;
  width: 25px;
}
.LogoutLastModal p {
  font-weight: 500;
  font-size: 22px;
  color: #000000;
  margin-block: 20px;
  text-align: center;
}

.css-1wnsr1i {
  border-radius: 18px;
  padding: 20px !important;
  border: none !important;
}

.LogOutBTNS {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  border-top: 1px solid #dfdada;
  padding: 10px;
}
.LogOutBTNS button {
  border: none;
  background-color: transparent;
}
.LogOutBTNSONe {
  color: #e34444;
  font-weight: 400;
  font-size: 18px;
}
.LogOutBTNSTwo {
  font-weight: 400;
  font-size: 18px;
  color: #00afef;
}
@media (max-width: 1243px) {
  .saidbar-main .setting .rowww img {
    width: 30px;
    height: 30px;
  }

  .saidbar-main .menu ul li a svg {
    width: 24px;
    height: 24px;
    margin-right: 14px;
  }

  .saidbar-main .setting .rowww .names p {
    font-size: 10px !important;
  }

  .saidbar-main .setting i {
    font-size: 18px !important;
  }
}

@media (max-width: 1049px) {
  .rowww > * {
    flex-shrink: 0;

    padding-right: 0px;
    padding-left: 0px;
    margin-top: 0px;
  }

  .saidbar-main .setting .rowww .names p {
    font-size: 10px !important;
  }

  .saidbar-main .setting i {
    font-size: 20px !important;
  }
}

@media (max-width: 1186px) {
  .adminpenal-main .right-said .dashboard .selectbox .box {
    width: 200px;
    padding: 20px;
  }

  .profit {
    font-size: 20px !important;
    font-weight: 600 !important;
  }

  .plas {
    font-size: 15px !important;
    font-weight: 400 !important;
  }
}

@media (max-width: 1191px) {
  .adminpenal-main .right-said .dashboard {
    margin: 2rem 0px 0px 1.5rem;
  }
}

@media (max-width: 1050px) {
  .adminpenal-main .right-said .dashboard .selectbox .box {
    width: 170px;
    height: 100px;
    padding: 15px;
  }

  .adminpenal-main .right-said .dashboard .selectbox .box p {
    font-size: 14px;
  }

  .profit {
    font-size: 17px !important;
  }

  .plas {
    font-size: 13px !important;
  }

  .adminpenal-main .right-said .dashboard .topbox h2 {
    font-size: 25px;
  }

  .adminpenal-main .right-said .dashboard .topbox select {
    padding: 2px 2px;
    font-size: 12px;
  }
}

@media (max-width: 972px) {
  .saidbar-main .setting .rowww .names p {
    font-size: 8px !important;
  }
}

@media (max-width: 900px) {
  .hamburger {
    display: block;
  }

  .saidbar-main .setting .rowww .names p {
    font-size: 10px !important;
  }

  .search-top .search button {
    font-size: 1.5rem;
    color: #b8bed7;
    margin: 0px 10px 0px 10px;
    margin-left: 30px;
  }

  .saidbar-main {
    width: 250px;
    height: auto;
    position: absolute;
    top: 0;
    left: -250px;
    background-color: #101828;
    transition: left 0.3s ease;
  }

  .saidbar-main .setting {
    display: flex;
    justify-content: center;
    gap: 10px;
    align-items: center;
    margin-top: 63px;
  }

  .saidbar-main.open {
    left: 0;
    height: 90vh;
    z-index: 10;
  }

  .saidbar-main .setting {
    display: flex;
    justify-content: center;
    gap: 10px;
    width: 100%;
    align-items: center;
    padding: 0px 20px;
  }

  .saidbar-main .setting .rowww {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
  }

  .saidbar-main .setting .rowww img {
    width: 40px;
    height: 40px;
    margin: 0px;
    padding: 0px;
  }

  .saidbar-main .setting .rowww .names p {
    font-size: 14px;
    color: #282d4f;
    margin: 0px;
  }

  .saidbar-main .setting .rowww .names .admin-name {
    font-size: 14px;
    color: #b5b6bb;
  }

  .hide {
    display: none;
  }

  .hamburger svg {
    width: 30px;
    height: 30px;
    color: #007dfa;
    position: absolute;
    top: 16px;
    z-index: 1000;
    left: 18px;
  }
}

@media (max-width: 724px) {
  .adminpenal-main .right-said .dashboard .selectbox .box {
    width: 150px;
    height: 80px;
    padding: 12px;
  }

  .adminpenal-main .right-said .dashboard .selectbox .box p {
    font-size: 12px;
  }

  .profit {
    font-size: 15px !important;
  }

  .plas {
    font-size: 11px !important;
  }
}

@media (max-width: 640px) {
  .adminpenal-main .right-said .dashboard .selectbox .box {
    width: 130px;
    height: 60px;
    padding: 11px;
    border-radius: 8px;
  }

  .adminpenal-main .right-said .dashboard .selectbox .box p {
    font-size: 9px;
  }

  .profit {
    font-size: 12px !important;
  }

  .plas {
    font-size: 8px !important;
  }

  .Balance .Income h1 {
    font-size: 14px !important;
    margin: 0px;
  }

  .Balance .Income p svg {
    height: 10px !important;
    width: 10px !important;
  }

  .Balance .Income p {
    font-size: 10px !important;
  }

  .Balance .Income h6,
  .adminpenal-main
    .right-said
    .dashboard
    .selectchart
    .chart
    .Balance
    .Income
    h5 {
    font-size: 10px !important;
    font-weight: 450;
  }

  .Balance h3 {
    font-size: 11px !important;
  }
}

@media (max-width: 556px) {
  .adminpenal-main .right-said .dashboard {
    margin: 1rem 0px 0px 0.7rem;
  }

  .selectbox {
    display: flex;
    flex-direction: column;
  }

  .adminpenal-main .right-said .dashboard .selectbox .box {
    width: 100%;
    height: 80px;
    padding: 12px;
    border-radius: 8px;
    margin-top: 10px;
  }

  .adminpenal-main .right-said .dashboard .selectbox .box p {
    font-size: 12px;
  }

  .profit {
    font-size: 15px !important;
  }

  .plas {
    font-size: 11px !important;
  }
}

@media (max-width: 494px) {
  .adminpenal-main .right-said .dashboard .topbox {
    margin-block: 20px;
  }

  .adminpenal-main .right-said .dashboard .topbox h2 {
    font-size: 18px;
    margin-bottom: 0px;
  }

  .Balance .Income h1 {
    font-size: 10px !important;
    margin: 0px;
  }

  .Balance .Income p svg {
    height: 7px !important;
    width: 7px !important;
  }

  .Balance .Income p {
    font-size: 7px !important;
  }

  .Balance .Income h6,
  .adminpenal-main
    .right-said
    .dashboard
    .selectchart
    .chart
    .Balance
    .Income
    h5 {
    font-size: 7px !important;
    font-weight: 450;
  }

  .Balance h3 {
    font-size: 9px !important;
  }
}
