.my-signupmodal123 {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  border-radius: 44px;
  padding: 30px;
  width: 35%;
}
.RoleCardMain {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 93vh;
  background-color: #f4f5fa;
}
.my-modale-child-1 {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.my-modale-child-1 img {
  width: 120px;
  height: 120px;
}

.my-modale-child-1 h2 {
  font-size: 30px;
  font-weight: 500;
  color: #000000;
  margin: 0;
}

.my-modale-child-2 p {
  font-size: 22px;
  font-weight: 400;
  color: #000000;
  margin: 0;
}

.my-modale-child-2 span {
  font-size: 22px;
  font-weight: 400;
  color: #0f5dfa;
  margin: 0;
}

.my-modale-child-3 {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.my-modale-child-3 p {
  font-size: 18px;
  font-weight: 400;
  margin: 0;
  color: #8b8d97;
}

.my-modale-child-3 span {
  font-size: 18px;
  font-weight: 400;
  margin: 0;
  color: #c4a77e;
}

.my-modale-child-4 {
  display: flex;
  align-items: center;
  gap: 32px;
}

.modal-button-1 {
  width: 180px;
  height: 50px;
  border-radius: 10px;
  background-color: #000000 !important;
  color: #ffffff;
}

.modal-button-2 {
  width: 180px;
  height: 50px;
  border-radius: 10px;
  background-color: #ffffff !important;
  color: #000000 !important;
  border: solid 1px #101828 !important;
}
@media (max-width: 688px) {
  .login-main {
    width: 100% !important;
  }
}
@media (max-width: 450px) {
  .login-main {
    padding: 44px 20px !important;
  }
  .login-main-child-1 img {
    width: 60px !important;
    height: 60px !important;
  }
  .input-div input {
    margin-left: 0px !important;
  }
  .input-icon svg {
    font-size: 25px !important;
  }
}
@media (max-width: 370px) {
  .login-main-child-2-child-1 {
    height: 50px !important;
  }
  .forgetpass a {
    font-size: 12px !important;
    margin-top: 5px !important;
  }
  .login-main {
    gap: 30px !important;
  }
  .login-main-child-3 button {
    width: 150px !important;
    height: 45px !important;
  }
  .login-main-child-3 h2 {
    font-size: 12px !important;
  }
}
@media (max-width: 350px) {
  .TopLogoMain {
    padding: 10px 15px;
  }
  .login-main-child-2-child-1 {
    gap: 5px !important;
  }
  .login-main-child-1 p {
    font-size: 12px !important;
  }
}
@media (max-width: 330px) {
  .login-main {
    padding: 44px 15px !important;
  }
  .input-div input {
    font-size: 14px;
  }
  .input-icon svg {
    font-size: 22px !important;
  }
  .showpass svg {
    font-size: 18px !important;
  }
  .input-div input::placeholder {
    font-size: 14px !important;
  }
  .login-main-child-1 img {
    width: 50px !important;
    height: 50px !important;
  }
  .login-main-child-1 h1 {
    font-size: 18px !important;
  }
  .login-main {
    gap: 20px !important;
  }
  .login-main-child-3 button {
    width: 100px !important;
    height: 40px !important;
    font-size: 14px !important;
  }
}
