.CommissionMain {
  padding: 20px;
}
.CommissionTop {
  background-color: #101828;
  padding: 24px;
  border-radius: 8px;
}
.CommissionTop h3 {
  color: #fff;
  margin-bottom: 0px;
  font-weight: 600;
  font-size: 20px;
}
.CommissionBody {
  padding: 20px;
  background-color: #f3f3f4;
  border: 1px solid #d2d2d299;
  margin-top: 20px;
  border-radius: 10px;
}

.CommissionBody .CommsiTabAll {
  border-bottom: 2px solid #0f5dfa;
  width: 28px;
}
.CommissionBody h5 {
  font-weight: 600;
  font-size: 20px;
  color: #000000;
  margin-bottom: 0px;
  margin-top: 30px;
}
.Commissionbtns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.CommCard {
  background-color: #fff;
  height: 90px;
  padding: 0px 40px;
  border-radius: 6px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 6px;
}
.CommCard p {
  font-weight: 500;
  font-size: 14px;
  color: #534b4b;
  margin-bottom: 0px;
}
.CommCard p span {
  color: #0f5dfa;
}
.CommCard h1 {
  font-weight: 700;
  font-size: 24px;
  margin-bottom: 0px;
  color: #4a4646;
}
.Commissionbtns button {
  height: 47px;
  width: 120px;
  background-color: #101828;
  color: #fff;
  border: none;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
}
.CommissionTable {
  width: 100%;
  margin-top: 20px;
}
.CommissionTable table {
  width: 100%;
}
.CommissionTable table th,
.CommissionTable table td {
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 1px solid #dbdade;
}
.CommissionTable table th {
  font-weight: 500;
  font-size: 13px;
  color: #8b909a;
}
.CommissionTable table td {
  font-weight: 400;
  font-size: 15px;
  color: #23272e;
}
.CommissionTable p {
  margin-bottom: 0px;
  cursor: pointer;
}
@media (max-width: 1313px) {
  .CommCard {
    padding: 0px 20px;
  }
  .CommissionTable table td {
    font-size: 12px;
  }
}
@media (max-width: 1156px) {
  .CommCard h1 {
    font-size: 20px;
  }
  .CommCard p {
    font-size: 12px;
  }
  .Commissionbtns button {
    height: 40px;
    width: 100px;
    font-size: 13px;
  }
}
@media (max-width: 886px) {
  .CommissionMain {
    padding: 10px 20px;
  }
  .CommissionTable table th,
  .CommissionTable table td {
    padding: 10px;
  }
  .CommissionTable {
    max-width: 852px;
    overflow-x: scroll;
  }
  .CommCard h1 {
    font-size: 18px;
  }
  .CommCard p {
    font-size: 10px;
  }
}
@media (max-width: 760px) {
  .Commissionbtns {
    align-items: start;
    flex-direction: column;
  }
  .CommCard {
    width: 100%;
  }
  .Commissionbtns button {
    margin-top: 10px;
  }
  .CommissionTable table th {
    font-size: 10px;
  }
  .CommissionTable table td {
    font-size: 10px;
  }
  .CommissionBody h5 {
    font-size: 18px;
    margin-top: 14px;
  }
  .CommissionTop {
    padding: 15px 10px;
    margin-left: 36px;
  }
}
@media (max-width: 376px) {
  .CommissionTop {
    margin-left: 12px !important;
  }
}
