.reset {
  background-color: #f4f5fa;
  max-width: 100vw;
  height: 100vh;
}

.resetform {
  background-color: #fff;
  display: flex;
  box-sizing: border-box;
  max-width: 650px;
  padding: 2rem;
  border-radius: 12px;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  padding: 50px 100px;
  width: 100%;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 152px);
  margin-inline: 2rem;
}

.resettext {
  text-align: center;
  line-height: 1.5;
}

.loginputs {
  display: flex;
  width: 90%;
  margin: 0 auto;
  justify-content: flex-start;
  border-radius: 8px;
  align-items: center;
  padding-block: 0.9rem;
  background: var(--Input-defaultBackground, rgba(239, 241, 249, 0.6));
}

.loginputs > input {
  border: none;
  outline: none;
  width: 100%;
  background-color: transparent;
}

.meslogo {
  width: 30px;
  height: 24px;
  object-fit: contain;
  margin-inline: 16px;
}

.mainlogcontainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.loginputs > input::placeholder {
  color: var(--Black-2, #abafb1);
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.08px;
}

.signup {
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: center;
  margin-top: 20px;
}

.resettext2 {
  color: var(--Greyscale-500, #64748b);
  text-align: center;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.07px;
}

.signup span {
  color: #000;
  font-size: 16px;
  font-weight: 600;
  text-decoration: underline;
}

/* OTP Section */
.OTP-text p {
  color: #64748b;
  font-size: 15px;
  font-weight: 400;
}

.OTP-text span {
  color: #000;
  font-size: 16px;
  font-weight: 400;
}

.Select-Otp {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  margin: 0;
}

.logintext h2 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  color: #0f172a;
}

/* OTP Input Fields */
.opt-code-number {
  width: 56px;
  height: 56px;
  border: 1px solid #b67571;
  border-radius: 8px;
  color: #000;
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  outline: none;
  transition: box-shadow 0.2s, border-color 0.2s;
  background: #fff;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.opt-code-number:focus {
  border-color: #000;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.OTPbtn {
  border: none;
  width: 100%;
  background: #000;
  height: 56px;
  border-radius: 12px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 15px;
  font-weight: 800;
  cursor: pointer;
  transition: background 0.2s;
}

.OTPbtn:hover {
  background: #333;
}

/* Resend Code Section */
.Resend-Code {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.Resend-Code p {
  color: #0f172a !important;
  font-size: 16px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  margin: 0;
}

.otp-span {
  color: #c4a77e;
  font-size: 16px;
  font-weight: 700;
  font-family: "Inter", sans-serif;
  cursor: pointer;
  transition: color 0.2s;
  margin: 0;
}

.Resend-Code span {
  color: #747474;
  font-size: 16px;
  font-weight: 600;
  font-family: "Inter", sans-serif;
  margin-left: 10px;
}

.Resend-Code p.disabled {
  cursor: not-allowed;
  opacity: 0.6;
}
