.CardMAIN {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  margin-top: 30px;
}
.CardMAIN .BrokerCards {
  height: 240px;
  background-color: #fff;
  border-radius: 18px;
  padding: 20px;
  width: 32%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.CardMAIN .Cardsss p,
.CardMAIN .Cardsss h4,
.CardMAIN .Cardsss h2,
.CardMAIN .Cardsss h6 {
  margin: 0px;
}
.CardMAIN .Cardsss p {
  font-size: 40px;
}
.Cardsmonth {
  display: flex;
  align-items: center;
  gap: 4px;
}
.CardMAIN .Cardsss h2 {
  font-weight: 600;
  font-size: 36px;
  color: #101828;
}

.CardMAIN .Cardsss span {
  font-size: 22px;
  color: #12b76a;
}
.CardMAIN .Cardsmonth p {
  font-weight: 500;
  font-size: 14px;
  color: #12b76a;
}
.CardMAIN .Cardsmonth h6 {
  color: #667085;
  font-weight: 500;
  font-size: 14px;
}
@media (max-width: 1287px) {
  .CardMAIN .Cardsss h2 {
    font-size: 26px;
  }
  .CardMAIN .Cardsss h4 {
    font-size: 20px;
  }
  .CardMAIN .Cardsss {
    height: 220px;
  }
  .saidbar-main .setting {
    gap: 0px;
    padding: 0px;
  }
  .saidbar-main .setting .rowww {
    gap: 8px;
    padding: 5px;
  }
}
@media (max-width: 1151px) {
  .CardMAIN .Cardsss h4 {
    font-size: 16px;
  }
  .CardMAIN .Cardsss h2 {
    font-size: 22px;
  }
  .CardMAIN .Cardsss {
    gap: 6px;
    height: 200px;
  }
  .CardMAIN {
    margin-top: 20px;
  }
  .PrivateMain {
    margin-top: 20px;
  }
  .saidbar-main .LinkLogo h3 {
    font-size: 20px;
  }
  .GrayHome {
    padding: 20px !important;
  }
  .WelcomeHead h1 {
    font-size: 22px !important;
  }
  .WelcomeHead p {
    font-size: 14px !important;
  }
  .WelcomeHead {
    gap: 6px !important;
  }
  .CardMAIN .Cardsss span {
    font-size: 20px;
  }
  .CardMAIN .Cardsmonth h6 {
    font-size: 12px;
  }
  .CardMAIN .Cardsmonth p {
    font-size: 12px;
  }
  .saidbar-main .logo {
    height: 45px;
  }
}
