.listing-text {
  margin-bottom: 0px !important;
}
.CommiPAdd {
  margin: 20px;
}
.CommisioNFlex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 24%;
  margin-top: 10px;
}
.CommisioNFlex1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 24%;
}
.commisBodyPad {
  margin: 20px;
}
.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  color: #101828b2 !important;
  background-color: unset !important;
  border-color: #f3f3f4 !important;
  border-bottom: 2px solid #0f5dfa !important;
}
.commisBodyPad .nav-tabs {
  border-bottom: none !important;
}
.nav-link {
  color: #101828b2 !important;
  font-weight: 500 !important;
}
.commisBodyPad .CommiPAdd {
  margin: 20px 0px !important;
}
.CommoChild {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
.CommoOne {
  padding: 30px 20px;
  background-color: #fff;
  border-radius: 10px;
  width: 48%;
}
.CommoTwo {
  padding: 30px 20px;
  background-color: #fff;
  border-radius: 10px;
  width: 48%;
}
.CommoChild h4 {
  font-weight: 500;
  font-size: 20px;
  color: #333333;
}
.CommoChild h5 {
  font-weight: 400;
  font-size: 20px;
  color: #333333;
  margin-top: 0px;
}
.CommTwoFlex {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
}
.CommoTwo table {
  width: 100%;
}
.CommoTwo table th {
  font-weight: 600;
  font-size: 20px;
  color: #101828;
  padding: 0px 10px;
}
.CommoTwo table td {
  padding: 20px 10px;
  font-weight: 400;
  font-size: 20px;
  color: #333333;
}
.CommoChildLast {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  width: 80%;
}
.CommoChildLast h6 {
  font-weight: 500;
  font-size: 20px;
  color: #1f1fff;
}
.CommoChildLast h4 {
  font-weight: 500;
  font-size: 20px;
  color: #333333;
}
.CommoChild button {
  height: 46px;
  padding: 0px 10px;
  background-color: #101828;
  color: #fff;
  font-weight: 400;
  font-size: 20px;
  border-radius: 50px;
  border: none;
}
.CommoOneTwo p {
  padding: 30px 20px;
  background-color: #fff;
  border-radius: 10px;
}
@media (max-width: 1327px) {
  .CommoTwo table td {
    font-size: 12px;
  }
  .CommoTwo table th {
    font-size: 15px;
  }
  .CommoChildLast h4 {
    font-size: 16px;
  }
  .CommoChildLast h6 {
    font-size: 14px;
  }
}
@media (max-width: 1189px) {
  .CommisioNFlex1 {
    width: 39.7%;
  }
  .CommisioNFlex {
    width: 48.2%;
  }
  .CommissionTop h3 {
    color: #fff;
    margin-bottom: 0px;
    font-weight: 600;
    font-size: 16px;
  }
  .CommoChild h4 {
    font-size: 14px;
  }
  .CommoChild h5 {
    font-size: 14px !important;
  }
  .CommoChild button {
    height: 33px;
    font-size: 13px;
  }
}
@media (max-width: 767px) {
  .CommisioNFlex {
    width: 100%;
  }
  .CommisioNFlex1 {
    width: 100%;
  }
  .ProfileMain .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link {
    margin-top: 0px !important;
  }
  .CommoChildLast {
    width: 100%;
  }
}
@media (max-width: 760px) {
  .CommTwoFlex {
    flex-direction: column;
  }
  .CommoOne {
    width: 100%;
    margin-bottom: 10px;
  }
  .CommoTwo {
    width: 100%;
  }
}
@media (max-width: 408px) {
  .commisBodyPad .CommiPAdd {
    margin: 10px 0px !important;
  }
  .CommissionBody {
    padding: 10px;
  }
  .CommoChild h4 {
    font-size: 12px;
  }
  .CommoChild h5 {
    font-size: 12px !important;
  }
  .CommoTwo table td {
    padding: 10px;
  }
  .CommoChildLast h6 {
    font-size: 12px;
  }
  .CommoChildLast h4 {
    font-size: 14px;
  }
  .CommoTwo table th {
    font-size: 13px;
  }
  .CommoOneTwo p {
    padding: 20px 10px;
    font-size: 14px;
  }
}
@media (max-width: 380px) {
  .CommissionTop h3 {
    font-size: 14px;
  }
  .nav-link {
    font-size: 14px !important;
  }
  .CommoTwo {
    padding: 20px 10px;
  }
  .CommoOne {
    padding: 20px 10px;
  }
  .CommoTwo table td {
    padding: 10px 0px;
  }
  .CommoTwo table th {
    padding: 10px 0px;
  }
  .list-with-arrow {
    margin-left: 32px !important;
  }
}
