.changepassword-main {
  width: 100%;
  height: 100vh;
  background-color: #f5f4fa;
  display: flex;
  align-items: center;
  justify-content: center;
}

.changepassword-iner-main {
  width: 680px;
  height: auto;
  background-color: #fff;
  border-radius: 12px;
  display: flex;
  gap: 30px;
  flex-direction: column;
  align-items: center;
  padding: 40px 30px;
}

.changepassword-iner-main-child-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.changepassword-iner-main-child-1 img {
  width: 80px;
  height: 84px;
}

.changepassword-iner-main-child-1 h2 {
  font-size: 20px;
  font-weight: 500;
  color: #000;
  margin: 0;
}

.changepassword-iner-main-child-1 p {
  font-size: 14px;
  font-weight: 400;
  color: #8b8d97;
  margin: 0;
}

.login-main-child-2-child-1 {
  width: 100%;
  height: 55px;
  background-color: #eff1f999;
  border-radius: 8px;
  padding: 0 16px;
  display: flex;
  gap: 16px;
  align-items: center;
}

.input-icon svg {
  font-size: 30px;
  color: #6e7079;
}

.input-div {
  width: 100%;
}

.input-div input {
  width: 100%;
  height: 55px;
  outline: none;
  border: none;
  background-color: transparent;
}

.input-div input::placeholder {
  font-size: 16px;
  font-weight: 400;
  color: #abafb1;
}

.showpass svg {
  font-size: 20px;
  color: #6e7079;
}

.changepassword-iner-main-child-2 {
  width: 92%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.changepassword-iner-main-child-2 button {
  width: 180px;
  height: 55px;
  color: #fff;
  background-color: #101828;
  border-radius: 10px;
  border: none;
  outline: none;
  font-size: 18px;
  font-weight: 400;
}
