.ImportMain {
  background-color: #fff;
  padding: 30px;
  margin: 20px;
  border-radius: 12px;
}
.ImportContainer {
  border: 2px dashed #6e7485;
  height: 171px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}

.ImportImg {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
}
.importH3 h3 {
  font-weight: 500;
  font-size: 16px;
  color: #000000;
  margin: 0px;
}
.importH3 p {
  font-weight: 400;
  font-size: 16px;
  color: #6e7485;
  margin: 0px;
}
.ImportBtns {
  display: flex;
  align-items: center;
  justify-content: end;
  margin-top: 20px;
}
.ImportBtns button {
  background-color: #0c243c;
  color: #ffffff;
  font-weight: 600;
  font-size: 16px;
  height: 48px;
  padding: 0px 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.ImportBtns button svg {
  font-weight: 600;
  font-size: 30px;
}
.selectedPreview {
  height: 120px;
  width: 120px;
}
.list-with-arrow svg {
  cursor: pointer;
}
