.FilterBtnsReport {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #fff;
  border: 1px solid #d2d0d0;
}
.FilterButtons {
  display: flex;
  align-items: center;
  gap: 20px;
}
.FilterButtons button {
  padding: 7px 14px;
  font-weight: 600;
  font-size: 18px;
  color: #101828;
  border: none;
  border-radius: 10px;
}
.ReportSearch {
  border: none;
  border-radius: 12px;
  padding: 10px;
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  gap: 10px;
}
.ReportSearch input {
  outline: none;
  border: none;
  background-color: transparent;
}
.ReportTable {
  background-color: #fff;
}
.ReportTable table {
  width: 100%;
}
.ReportTable table th {
  font-weight: 400;
  font-size: 18px;
  color: #101828;
  padding: 10px 20px;
}

.HotelImage {
  display: flex;
  align-items: center;
  gap: 10px;
}
.HotelImage img {
  height: 46px;
  width: 74px;
  border-radius: 10px;
}
.HotelImage p {
  font-weight: 500;
  font-size: 16px;
  color: #101828;
  margin: 0px;
}
.ReportTable tbody tr {
  border-top: 10px solid #f7f7f7;
  border-right: 20px solid #f7f7f7;
  border-left: 20px solid #f7f7f7;
  border-radius: 10px;
}
.ReportTable tbody td {
  padding: 12px;
}
.UsersViews {
  display: flex;
  align-items: center;
}

.avatar-group {
  display: flex;
  align-items: center;
}

.avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid white;
  object-fit: cover;
  margin-left: -10px;
}

.avatar:first-child {
  margin-left: 0;
}

.extra-count {
  margin-left: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #000;
}
.FilterButtons .active-button {
  background-color: black;
  color: white;
}

.FilterButtons button:hover {
  background-color: #ddd;
}
.UsersTable .CircularProgressbar {
  width: 30%;
  height: 45px !important;
}
.UsersTable .CircularProgressbar .CircularProgressbar-path {
  stroke: #000 !important;
}
.UsersTable .CircularProgressbar .CircularProgressbar-text {
  fill: #000 !important;
  font-size: 23px !important;
}
