.UserFilterBtns {
  display: flex;
  align-items: center;
  gap: 20px;
  border-top: 1px solid #d2d0d0;
  border-bottom: 1px solid #d2d0d0;
  padding: 20px;
}
.UserFilterBtns button {
  height: 40px;
  padding: 0px 25px;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 18px;
  color: #101828;
}
