.PtgCard {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}
.PtgCard span {
  color: #f04438;
  font-weight: 500;
  font-size: 14px;
}
.PtgCard h6 {
  font-weight: 500;
  font-size: 14px;
  color: #667085;
  margin: 0px;
}
