  display: flex;
  align-items: center;
  justify-content: center;
} */
.MiNiMain2 .btn-primary {
  background: transparent !important;
  color: #101828 !important ;
  box-shadow: 0px 1px 4px 0px #00000040;
}
.Mini-Main-Btns1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Mini-Main-Btns1 svg {
  color: #20ac00;
}
.AgentFlex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.AgentSearch {
  display: flex;
  align-items: center;
  gap: 10px;
}
.AgentSearch .input svg {
  width: 20px;
  height: 20px;
  color: #8e8b8b;
}
.AgentSearch .input {
  border: 1px solid #8e8b8b;
  border-radius: 6px;
  padding: 0px 2px;
}
.AgentSearch .input input {
  padding: 7px 11px;
  border: none;
  outline: none;
  color: #8e8b8b;
  font-size: 12px;
  font-weight: 400;
  width: 90%;
}
.invitePeople {
  border: 1px solid #8e8b8b;
  border-radius: 6px;
  padding: 5px 11px;
  display: flex;
  align-items: center;
  gap: 7px;
  font-weight: 500;
  font-size: 14px;
  color: #101828;
  cursor: pointer;
}
.modal-content {
  border-radius: 20px !important;
}
.AgentModal .modal-header {
  border-bottom: 0px !important;
}
.AgentModalMain {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  padding: 0px 0px 30px 0px;
}
.AgentModalMain h4 {
  font-weight: 600;
  font-size: 30px;
  color: #333333;
  margin-bottom: 0px;
}
.AgentModalMain button {
  width: 80%;
  background-color: #101828;
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  border-radius: 12px;
  padding: 10px 0px;
}
.AgentModalSearch .input {
  width: 100%;
  padding: 4px;
  border-radius: 12px;
}
.AgentModalSearch .input svg {
  color: #000000;
}
.AgentModalSearch {
  width: 80%;
}
