.custom-radio input[type="radio"] {
  display: none;
}

.custom-radio .radio-mark {
  height: 20px;
  width: 20px;
  border: 2px solid #101828;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.custom-radio input[type="radio"]:checked + .radio-mark::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #101828;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
