.TextFlex .checkmark {
  height: 22px;
  width: 26px;
  background-color: #fff;
  border: 1px solid #354052;
  display: inline-block;
  position: relative;
  border-radius: 4px;
  margin-right: 8px;
}

.TextFlex input[type="checkbox"]:checked + .checkmark::after {
  content: "";
  position: absolute;
  left: 6px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid #354052;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
