.listing-main-container {
  background-color: #f7f7f7;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
}
.listing-main-container p {
  margin: 0;
}
.listing-text {
  font-size: 16px;
  color: #333333;
}
.listing-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  padding: 10px 20px;
  margin: 20px 0px 0px 0px;
}
.list-with-arrow {
  display: flex;
  align-items: center;
  gap: 21px;
}

.list-upload-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.list-upload-handler {
  display: flex;
  align-items: center;
  gap: 8px;
  padding-inline: 16px;
  padding-block: 7px;
}

.connect-zapier {
  background-color: #101828;
  border-radius: 8px;
  color: white;
}

.import-listing {
  border: 1px solid #d0d5dd;
  border-radius: 8px;
  color: #344054;
}

.listing-cards-container {
  display: flex;
  width: calc(90% - 50px);
  gap: 20px;
  margin: 20px 0px 0px 20px;
}
.listing-card {
  background-color: white;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 25%;
  height: 198px;
  row-gap: 5px;
}

.listing-card p {
  font-size: 16px;
  font-weight: 500;
  color: #101828;
}
.listing-card h4 {
  font-size: 36px;
  font-weight: 600;
  color: #101828;
}
.listing-card .icon {
  font-size: 30px;
  color: #101828;
}
.listing-card .eye {
  color: #1990ff;
}

.listing-card h6 {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 5px;
  color: #667085;
  font-size: 14px;
  font-weight: 500;
}
.listing-card h6 span {
  color: #f04438;
}
.addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  height: 40px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  border: none;
}
.addPropertyButton svg {
  height: 22px;
  width: 22px;
}
