.ServiceMain {
  padding: 20px;
}
.ServiceMain .ReportTable tbody tr {
  border-top: 10px solid #f7f7f7;
  border-right: 0px !important;
  border-left: 0px !important;
  border-radius: 10px;
}
.ServiceMain .ReportTable table th {
  font-weight: 400;
  font-size: 18px;
  color: #101828;
  padding: 10px;
  background-color: #f7f7f7 !important;
}
.ProfileService {
  display: flex;
  align-items: center;
  gap: 10px;
}
.ProfileService img {
  width: 46px;
  height: 46px;
  border-radius: 50%;
}
.ProfileService p {
  font-weight: 500;
  font-size: 16px;
  color: #101828;
  margin: 0px;
}
.ServiceMain .modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.ServiceMain .modalContent {
  position: relative;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  text-align: center;
  border-radius: 44px;
  width: 648px;
}
.ServiceMain .modalContent span {
  position: absolute;
  right: -5px;
  top: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.ServiceMain .modalContent img {
  width: 40%;
  margin-bottom: 2rem;
}

.ServiceMain .modalContent h4 {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
  color: #212121;
}
.ServiceMain .modalContent p {
  color: #212121;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 0px;
  margin-top: 10px;
}
.ServiceMain .modalContent .btns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
}

.ServiceMain .modalContent .btns button {
  width: 28%;
  height: 46px;
  border-radius: 12px;
  color: 16px;
  font-weight: 700;
  border: none;
}
.ServiceMain .modalContent .btns .Cancel {
  border: 1px solid #101828;
  background-color: #fff;
}
.ServiceMain .modalContent .btns .yesBtn {
  background-color: #101828;
  color: #ffffff;
}
.ServiceMain .Mini-Main-Btns1 svg {
  color: #101828;
}
.ServiceAdd {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ServiceAdd button {
  height: 46px;
  padding: 0px 10px;
  border-radius: 12px;
  border: none;
  background-color: #101828;
  font-weight: 400;
  font-size: 18px;
  color: #ffffff;
}
.ServiceMain .input-div-payment12 {
  width: 100% !important;
  display: flex;
  align-items: start;
  flex-direction: column;
}
.ServiceMain .input-div-payment {
  width: 48%;
  display: flex;
  align-items: start;
  flex-direction: column;
}
.ServiceMain .input-div-payment12 input {
  width: 100%;
  height: 45px;
  font-size: 16px;
  outline: none;
  padding: 0px 10px;
  font-weight: 400;
  background-color: #fff;
  border-radius: 8px;
  border: 1px solid #d0d5dd;
}
.ServiceMain .input-div-payment12 textarea {
  font-weight: 400;
  background-color: #fff;
  border-radius: 8px;
  border: 1px solid #d0d5dd;
  font-size: 16px;
  width: 100%;
  padding: 10px;
  outline: none;
}
.ServiceMain h3 {
  font-weight: 400;
  font-size: 16px;
  color: #9f9f9f;
  margin-top: 10px;
  margin-bottom: 0px;
}
.ServiceBtn {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 5px;
  background-color: #f3f3f3;
  border-radius: 10px;
  margin-top: 10px;
}
.ServiceFileMain {
  display: flex;
  align-items: start;
  flex-direction: column;
}
.ServiceFile {
  display: flex;
  align-items: center;
  gap: 10px;
}
.ServiceBtn h5 {
  font-weight: 400;
  font-size: 14px;
  color: #212121;
  margin-bottom: 0px;
}
.ServiceBtn button {
  height: 47px;
  border-radius: 8px;
  padding: 0px 10px;
  border: 1px solid #00000021;
  background-color: #fff;
  display: flex;
  align-items: center;
  gap: 6px;
  font-weight: 400;
  font-size: 16px;
  color: #212121;
}
.ServiceBtn button svg {
  height: 22px;
  width: 22px;
}
