p {
  margin: 0px;
}
.UsersCardsMain {
  display: flex;
  align-items: center;
  gap: 30px;
  padding: 30px 20px;
}
.UserCard {
  width: 23%;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid #c8c5c5;
  background-color: #fff;
}
.UserCard:hover {
  background-color: #101828;
}

.addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  height: 40px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  border: none;
}
.addPropertyButton svg {
  height: 22px;
  width: 22px;
}
.UserCard:hover h4,
.UserCard:hover h2 {
  color: #fff;
}
.UserCard:hover .Cardsmonth h2 {
  color: #fff;
}
.UserCard:hover .Cardsmonth h5 {
  background-color: #fff !important;
  color: #101828;
}
.UserProfileTable {
  display: flex;
  align-items: center;
  gap: 10px;
}
.UserCard p {
  height: 40px;
  width: 40px;
  background-color: #f5f6f6 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
}
.UserCard p svg {
  font-size: 26px;
  color: #101828 !important;
}
.UserCard h4 {
  margin-top: 12px;
  color: #847c7c;
  margin-bottom: 0px;
}

.UserCard .Cardsmonth {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}
.UserCard .Cardsmonth h2 {
  font-weight: 700;
  font-size: 32px;
  color: #101828;
  margin-bottom: 0px;
}
.UserCard .Cardsmonth h5 {
  color: #101828;
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 0px;
  background-color: #101828 !important;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-radius: 4px;
  color: #fff;
}
.UsersTabs {
  padding: 20px;
  border-top: 1px solid #d2d0d0;
  border-bottom: 1px solid #d2d0d0;
  background-color: #fff;
}
.userBtns {
  display: flex;
  align-items: center;
  gap: 30px;
}

.userBtns button {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  font-weight: 500;
  font-size: 18px;
  background-color: #f7f7f7;
}
.UsersTable {
  padding: 20px 0px;
  background-color: #fff;
  border-radius: 10px;
}
.UsersTable table {
  width: 100%;
  border-radius: 12px;
  border: 1px solid #e2e2e28a;
}
.UsersTable table th {
  padding: 20px;
  background-color: #f7f7f7;
  font-weight: 400;
  font-size: 18px;
  color: #101828;
}
.UsersTable table td {
  padding: 15px;
  color: #6d6d6d;
  font-weight: 500;
  font-size: 12px;
  background-color: #fff;
}
.UserProfileTbale {
  display: flex;
  align-items: center;
  gap: 10px;
}
.UserProfileTbale img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
}
.UsersTable button {
  padding: 10px 20px;
  background-color: #ff0000;
  border-radius: 12px;
  border: none;
  font-weight: 400;
  font-size: 12px;
  color: #fff;
}
.userBtns button.active {
  background-color: #101828;
  color: #fff;
}
.UserProfileTable img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
}
.userBtns button:hover {
  background-color: #101828;
  color: #fff;
}
@media (max-width: 1259px) {
  .UserCard {
    width: 48%;
  }
  .list-upload-handler {
    padding-inline: 10px;
    padding-block: 7px;
  }
}
@media (max-width: 900px) {
  .list-with-arrow {
    margin-left: 40px !important;
  }
  .listing-container {
    margin: 10px 0px 0px 0px !important;
  }
  .UserCard h4 {
    font-size: 18px;
  }
  .UserCard .Cardsmonth h2 {
    font-size: 24px;
  }
  .UserCard {
    padding: 14px;
  }
  .UsersTable table td {
    padding: 8px;
  }
  .userBtns button {
    padding: 7px 15px;
    font-size: 14px;
  }
  .listing-main-container .listing-container .list-upload-handler p {
    font-size: 12px;
  }
  .UsersTable table th {
    padding: 15px;
    font-size: 14px;
  }
  .UserProfileTable p {
    font-size: 12px;
  }
  .UsersTable button {
    padding: 10px !important;
  }
}
@media (max-width: 582px) {
  .UsersCardsMain {
    padding: 20px;
  }
  .UsersTable {
    max-width: 580px;
    overflow-x: scroll;
  }
  .UsersTable table td {
    font-size: 10px;
  }
  .UserProfileTable p {
    font-size: 10px;
    width: 60px;
  }
  .userBtns {
    gap: 20px;
  }
  .UserProfileTable {
    gap: 8px;
  }
  .UsersTable button {
    padding: 5px !important;
  }
}
@media (max-width: 436px) {
  .list-upload-info {
    gap: 5px;
  }
  .list-upload-handler {
    padding-inline: 7px;
    padding-block: 7px;
  }
  .UsersCardsMain {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0px;
  }
  .UserCard h4 {
    font-size: 14px;
  }
  .UserCard .Cardsmonth h2 {
    font-size: 20px;
  }
  .UserCard .Cardsmonth h5 {
    font-size: 12px;
  }
  .listing-main-container .listing-container .list-upload-handler p {
    font-size: 10px;
  }
  .import-listing svg {
    font-size: 16px !important;
  }
  .hamburger svg {
    top: 21px !important;
  }
}
@media (max-width: 376px) {
  .UserCard .Cardsmonth {
    margin-top: 10px;
  }

  .listing-container {
    padding: 10px;
  }
  .hamburger svg {
    width: 20px;
    height: 30px;
  }
  .listing-text {
    font-size: 14px;
  }
  .UsersCardsMain {
    flex-direction: column;
    gap: 10px;
  }
  .UserCard {
    width: 100%;
  }
}
