.forgetpass-main {
  background-color: #f4f5fa;
  height: 93vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 100px 0px; */
}

.Forgetpassword-main {
  width: 680px;
  height: auto;
  background-color: rgb(255, 255, 255);
  padding: 44px 35px;
  border-radius: 12px;
  gap: 44px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.forgetpass-taxt {
  text-align: center;
}

.forgetpass-taxt h1 {
  font-size: 24px;
  font-weight: 700;
  color: #0f172a;
}

.forgetpass-taxt p {
  font-size: 14px;
  font-weight: 400;
  padding: 0px 60px;
  color: #64748b;
}

.forgerpass-input-main {
  width: 100%;
  height: 56px;
  border-radius: 12px;
  border: 1px solid #b67571;
  display: flex;
  align-items: center;
}

.forgetpass-logo svg {
  font-size: 25px;
  color: #64748b;
  margin-left: 16px;
}

.input-forgetpass {
  width: 100%;
}

.input-forgetpass input {
  width: 96%;
  height: 56px;
  background-color: transparent;
  border-radius: 12px;
  outline: none;
  margin-left: 16px;
  border: none;
  font-size: 16px;
}

.forget-button {
  width: 100%;
  height: auto;
}

.forget-button button {
  width: 100%;
  height: 56px;
  border-radius: 12px;
  background-color: #101828;
  color: #fff;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 700;
}

.forgerpass-signin {
  width: 100%;
  text-align: center;
}

.forgerpass-signin a {
  color: #c4a77e;
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

.forgerpass-signins {
  width: 100%;
  text-align: center;
}

.forgerpass-signins p {
  color: #0f172a;
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

.forgerpass-signins a {
  color: #c4a77e;
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}
