.WelcomeMain {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.WelcomeMain .royal-modal .modal-content {
  border-radius: 10px !important;
}

.WelcomeBtns {
  display: flex;
  align-items: center;
  gap: 12px;
}
.WelcomeHead {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.WelcomeHead h1 {
  font-weight: 500;
  font-size: 30px;
  color: #101828;
  margin: 0px;
}
.WelcomeHead p {
  font-weight: 400;
  font-size: 16px;
  color: #667085;
  margin: 0px;
}
.WelcomeBtns button {
  height: 40px;
  padding: 0px 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 14px;
  border-radius: 8px;
  cursor: pointer;
}
.WelcomeOne {
  border: 1px solid #d0d5dd;
  background-color: #fff;
}
.WelcomeOne svg {
  height: 20px;
  width: 20px;
}
.WelcomeTwo {
  background-color: #101828;
  color: #fff;
  border: none;
}
.WelcomeTwo svg {
  height: 20px;
  width: 20px;
  color: #fff;
}

.addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  height: 40px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  border: none;
}
.addPropertyButton svg {
  height: 22px;
  width: 22px;
}

.main-div-modal {
  width: 100%;
  height: 500px;
  display: flex;
  flex-direction: column;
  border-radius: 10px !important;
  position: relative;
}

.close-div {
  height: 40px;
  width: 40px;
  display: flex;
  position: absolute;
  right: 0;
  top: 10px;
  align-items: center;
  justify-content: center;
  background-color: #cfc0c0;
  border-radius: 50px;
}

.NotiTOp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Notificationss .dropdown-menu.show {
  width: 520px !important;
  padding: 20px 10px !important;
  background-color: #ececec;
}
.Notificationss .dropdown-toggle::after {
  display: none !important;
}
.Notificationss .btn-primary {
  --bs-btn-color: #fff;
  --bs-btn-bg: #101828 !important;
  --bs-btn-border-color: none !important;
  --bs-btn-hover-border-color: none !important;
  box-shadow: none !important;
}
.Notificationss .btn:hover {
  color: #fff !important;
  background-color: #101828 !important;
  border-color: #fff !important;
}
.Notificationss .btn.show.btn:active {
  color: #fff !important;
  background-color: #101828 !important;
  border-color: #fff !important;
}
.NotiTOp h4 {
  font-weight: 500;
  font-size: 24px;
  color: #101828;
  margin: 0px !important;
}
.NotiTOp p {
  font-weight: 400;
  font-size: 18px;
  color: #007aff;
  margin: 0px;
  cursor: pointer;
}
.NotiCard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 20px;
  margin-top: 10px;
  border-radius: 6px;
}
.NotiImgMain {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 18px;
}
.NotiImgMain img {
  height: 54px;
  width: 54px;
  border-radius: 50%;
}
.NotiContact h3 {
  font-weight: 500;
  margin: 0px;
  font-size: 18px;
  color: #101828;
}
.NotiContact p {
  margin: 0px;
  font-weight: 500;
  font-size: 14px;
  color: #8c96ab;
}

.NotiHours p {
  margin: 0px;
  font-weight: 500;
  font-size: 14px;
  text-align: end;
  color: #8c96ab;
}
.NotiBtns {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}
.NotiBtns button {
  height: 30px !important;
  border-radius: 8px;
  padding: 0px 10px;
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  border: none;
}
.NotiBtns button:nth-child(1) {
  background-color: #ff0000;
}
.NotiBtns button:nth-child(2) {
  background-color: #20ac00;
}
.WelcomeMain .modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.WelcomeMain .modalContent {
  position: relative;
  background-color: #fff;
  padding: 40px 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  width: 455px;
}
.WelcomeMain .modalContent span {
  position: absolute;
  right: -5px;
  top: -10px;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.LinkDivMain h3 {
  font-weight: 600;
  font-size: 28px;
  color: #000000;
  margin-bottom: 0px;
  margin-top: 20px;
}
.CheckMain {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}
.CheckMain p {
  font-weight: 400;
  font-size: 16px;
  color: #000000;
  margin: 0px;
}
.CheckMain svg {
  color: #cccccc;
}
.ConnectBtns {
  margin-top: 30px;
  display: flex;
  align-items: center;
  gap: 20px;
}
.ConnectBtns button {
  padding: 10px 15px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
}

.ConnectBtns .NotNow {
  background-color: #ffffff;
  border: 1px solid #d0d5dd;
  color: #344054;
}
.ConnectBtns .ConnectNow {
  color: #ffffff;
  background-color: #101828;
  border: 1px solid #101828;
}
