.login-main {
  width: 685px;
  height: auto;
  background-color: #fff;
  border-radius: 12px;
  padding: 44px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.login-main-child-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.login-main-child-1 img {
  width: 70px;
  height: 70px;
}

.login-main-child-1 h1 {
  font-size: 20px;
  font-weight: 500;
  color: #000000;
  margin: 0px;
}

.login-main-child-1 p {
  font-size: 14px;
  font-weight: 400;
  margin: 0px;
  color: #8b8d97;
}

.login-main-child-2 {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.login-main-child-2-child-1 {
  width: auto;
  height: 55px;
  background-color: #eff1f999;
  border-radius: 8px;
  padding: 0px 16px;
  display: flex;
  gap: 16px;
  align-items: center;
  width: 100% !important;
}

.input-icon svg {
  font-size: 30px;
  color: #6e7079;
  margin: 0px;
  border-radius: 12px;
}

.input-div {
  width: 100%;
  height: auto;
}
.input-div input {
  width: 100%;
  height: 55px;
  outline: none;
  border: none;
  background-color: transparent;
}

.input-div input::placeholder {
  font-size: 16px;
  font-weight: 400;
  color: #abafb1;
}

.forgetpass {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: end;
}

.forgetpass a {
  font-size: 14px;
  color: #c4a77e;
  font-weight: 400;
  cursor: pointer;
}

.login-main-child-3 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-main-child-3 h2 {
  font-size: 14px;
  color: #8b8d97;
  font-weight: 400;
}

.login-main-child-3 a {
  font-size: 14px;
  color: #c4a77e;
  font-weight: 400;
  cursor: pointer;
}

.login-main-child-3 button {
  width: 180px;
  height: 55px;
  background-color: #101828;
  color: #fff;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 400;
}

.showpass svg {
  font-size: 20px;
  color: #6e7079;
}
