.CalendarMAin {
  padding: 20px;
  background-color: #fff;
}
.CalendarMAin .inputAndButton .input svg {
  width: 20px;
  height: 20px;
  color: #8e8b8b;
}
.CalendarMAin .inputAndButton .input {
  border: 1px solid #8e8b8b;
  border-radius: 6px;
  padding: 0px 2px;
  gap: 0px !important;
}
.CalendarMAin .inputAndButton .input input {
  padding: 7px 11px;
  border: none;
  outline: none;
  color: #8e8b8b;
  font-size: 12px;
  font-weight: 400;
  width: 90%;
}
.CalendarMAin .inputAndButton .addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  height: 40px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  border: none;
}
.CalendarMAin .inputAndButton .addPropertyButton svg {
  height: 22px;
  width: 22px;
}
.CalendarMAin .inputAndButton {
  display: flex;
  align-items: center;
  gap: 10px;
}
.CalendarTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.CalendarHead h2 {
  font-weight: 700;
  font-size: 24px;
  margin: 0px;
  color: #101828;
}
.CalendarHead p {
  font-weight: 400;
  font-size: 12px;
  color: #52555b;
  margin: 0px;
}
.CalendarFlex {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.CalendarEvents {
  width: 49%;
  height: 72vh;
  overflow-y: scroll;
}
.Event {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  box-shadow: 0px 2px 23px 0px #e4e6e9;
  height: 90px;
  padding: 0px 20px;
  border-radius: 6px;
}
.Event:hover {
  background-color: #101828;
}
.Event:hover h1,
.Event:hover p {
  color: #fff;
}
.Event:hover .Mini_MAin p {
  color: #000 !important;
}
.Event:hover .Mini_MAin .btn-primary {
  color: #101828 !important;
  background-color: #fff !important;
  border-radius: 50% !important;
}
.EventMAin {
  display: flex;
  align-items: center;
  gap: 20px;
}
.EventMAin h1 {
  font-weight: 500;
  font-size: 48px;
  color: #8c96ab;
  margin: 0px;
}
.EventMAin p {
  font-weight: 500;
  font-size: 15px;
  color: #6b7897;
  margin: 0px;
}
.EventsCount {
  height: 40px;
  padding: 0px 15px;
  background-color: #1018289c;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.EventsCount12 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.Event:hover .EventsCount {
  background-color: #fff;
}
.Event:hover h3 {
  color: #101828;
}
.EventsCount h3 {
  font-weight: 500;
  font-size: 24px;
  color: #ffffff;
  margin: 0px;
}
.Calnedar50 {
  width: 49%;
}
.CalendarLate {
  box-shadow: 0px 2px 23px 0px #ededed;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
}
.ClnDRed h6 {
  font-weight: 500;
  font-size: 15px;
  color: #6b7897;
}
.ClnDRed h2 {
  font-weight: 700;
  font-size: 28.5px;
  color: #ff0000;
  margin: 0px;
}
.ClnDRed p {
  font-weight: 400;
  font-size: 10.45px;
  color: #34485e;
  margin-bottom: 0px;
}
.BoXred {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #f2f3f3;
  padding: 2px;
}
.Clndr420 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.Clndr420 h4 {
  font-weight: 500;
  font-size: 48px;
  color: #8c96ab;
  margin: 0px;
}
.Clndr420 p {
  font-weight: 500;
  font-size: 15px;
  color: #a2abbf;
  margin-bottom: 0px;
}
.LastYear {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #101828;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.LastYear p {
  font-weight: 500;
  font-size: 15px;
  color: #ffffff;
  margin: 0px;
}
.LastYear svg {
  color: #fff;
  height: 18px;
  width: 18px;
}

.Mini_MAin .btn-primary {
  --bs-btn-color: #1018289c !important;
  --bs-btn-bg: transparent !important;
  --bs-btn-border-color: transparent !important;
  --bs-btn-hover-bg: transparent !important;
  --bs-btn-hover-border-color: transparent !important;
  --bs-btn-active-shadow: none !important;
  --bs-btn-focus-shadow-rgb: none !important;
}
.Mini_MAin .btn-check:checked + .btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check) + .btn:active {
  color: #000 !important;
  background-color: #fff !important;
  border-color: #fff !important;
}
.Mini_MAin .dropdown-toggle::after {
  display: unset !important;
  margin-left: 0px !important;
  vertical-align: 0px !important;
  content: "";
  border-top: none !important;
  border-right: none !important;
  border-bottom: 0;
  border-left: 0px !important;
}

.Mini-Main-Btns1 {
  margin: 4px;
  cursor: pointer;
}
.Mini_MAin hr {
  margin: 2px 0px;
}
.Mini-Main-Btns2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 4px;
  cursor: pointer;
}
.Mini-Main-Btns2 svg {
  color: #ff3b30;
}

