.CalendarMAin .modal {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modall {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.CalendarMAin .modalContent {
  position: relative;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 14px;
  width: 950px;
}
.modalContentt {
  position: relative;
  text-align: center;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 40px;
  width: 658px;
}
.modalContentt img {
  width: 40%;
  margin-bottom: 2rem;
}
.modalContentt .btns button {
  width: 48% !important;
}
.CalendarMAin .modalContentt span {
  position: absolute;
  right: 0px;
  top: -14px;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.CalendarMAin .modalContent span {
  position: absolute;
  right: -25px;
  top: -22px;
  width: 30px;
  height: 30px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.CalendarMAin .modalContent img {
  width: 40%;
  margin-bottom: 2rem;
}

.CalendarMAin .modalContent h4 {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
  color: #212121;
}
.CalendarMAin .modalContent p {
  color: #212121;
  font-size: 16px;
  font-weight: 400;
}
.AddNewEventMain {
  display: flex;
  justify-content: space-between;
}
.AddlAbels {
  width: 48%;
}
.AddlAbels label {
  font-weight: 600;
  font-size: 14px;
  color: #606060;
  margin-top: 15px;
}
.AddlAbels input {
  height: 52px;
  border-radius: 4px;
  border: 1px solid #d5d5d5;
  padding: 10px;
  background-color: #f5f6fa;
