}
.AddlAbels textarea {
  border-radius: 4px;
  border: 1px solid #d5d5d5;
  padding: 10px;
  background-color: #f5f6fa;
  outline: none;
  color: #606060;
  width: 100%;
}
.AllSwitch {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50%;
}
.AllSwitch input {
  height: unset !important;
  margin-top: 15px;
  cursor: pointer;
}
.AllSwitch .form-switch .form-check-input {
  width: 3em !important;
  box-shadow: none !important;
}
.AllSwitch .form-check-input:checked {
