  border-color: #fff !important;
}
.CalendarMAin .btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-block: 20px;
}
.CalendarMAin .btns button {
  width: 30%;
  height: 58px;
  border-radius: 16px;
  color: 16px;
  font-weight: 700;
  background-color: #1018281a;
  border: none;
}
.CalendarMAin .btns .yesBtn {
  background-color: #101828;
  color: #ffffff;
}
.Butttonss button {
  width: 47% !important;
}

.Mini_MAin .dropdown-menu {
  --bs-dropdown-link-active-bg: #fff !important;
}
@media (max-width: 1211px) {
  .react-datepicker__day,
  .react-datepicker__time-name {
    width: 2rem !important;
    line-height: 2rem !important;
  }
  .Event {
    margin-top: 10px;
    height: 80px;
  }
  .EventMAin h1 {
    font-size: 36px;
  }
  .EventMAin p {
    font-size: 12px;
  }
  .EventsCount {
    height: 34px;
    padding: 0px 10px;
  }
  .Clndr420 h4 {
    font-size: 36px;
  }
  .Clndr420 p {
    font-size: 12px;
  }
  .CalendarLate {
    padding: 10px;
  }
  .ClnDRed h2 {
    font-size: 23.5px;
  }
  .ClnDRed h6 {
    font-size: 13px;
  }
  .ClnDRed p {
    font-size: 9.45px;
  }
  .LastYear p {
    font-size: 13px;
  }
}
@media (max-width: 900px) {
  .CalendarHead {
    margin-left: 40px;
  }
}
@media (max-width: 800px) {
  .CalendarFlex {
    flex-direction: column;
  }
  .Calnedar50 {
    width: 100%;
  }
  .CalendarEvents {
    width: 100%;
    margin-top: 20px;
  }
}
@media (max-width: 610px) {
  .CalendarMAin .inputAndButton .addPropertyButton {
    height: 35px;
    padding: 0px 8px;
  }
  .CalendarMAin .inputAndButton .addPropertyButton svg {
    height: 16px;
    width: 16px;
  }
  .CalendarMAin .inputAndButton .input input {
    width: 50%;
  }
  .CalendarMAin .inputAndButton .input {
    width: 50%;
    border: 1px solid rgba(128, 128, 128, 0.562);
    border-radius: 6px;
    padding: 3px;
  }
  .CalendarTop {
    align-items: start !important;
    flex-direction: column;
  }
  .CalendarMAin .inputAndButton {
    margin-left: 40px;
  }
}
@media (max-width: 410px) {
  .react-datepicker__day-name,
  .react-datepicker__day,
  .react-datepicker__time-name {
    margin: 0 !important;
  }
}
@media (max-width: 376px) {
  .CalendarHead {
    margin-left: 20px;
  }
  .CalendarMAin .inputAndButton {
    margin-left: 20px;
  }
}
@media (max-width: 336px) {
  .Clndr420 h4 {
    font-size: 26px;
  }
  .Clndr420 p {
    font-size: 10px;
  }
  .ClnDRed h2 {
    font-size: 20px;
  }
  .ClnDRed p {
    font-size: 8px;
  }
  .ClnDRed h6 {
    font-size: 11px;
  }
  .EventsCount h3 {
    font-size: 18px;
  }
  .EventMAin {
    gap: 10px;
  }
  .Event {
    padding: 0px 10px;
  }
  .EventMAin p {
    font-size: 10px;
  }
  .EventMAin h1 {
    font-size: 32px;
  }
  .CalendarEvents {
    margin-top: 10px;
  }
}
