.WelcomeMain {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.WelcomeMain .royal-modal .modal-content {
  border-radius: 10px !important;
}
.InviteTWO {
  padding: 9px 11px !important;
}
.WelcomeBtns {
  display: flex;
  align-items: center;
  gap: 12px;
}
.WelcomeHead {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.WelcomeHead h1 {
  font-weight: 500;
  font-size: 30px;
  color: #101828;
  margin: 0px;
}
.WelcomeHead p {
  font-weight: 400;
  font-size: 16px;
  color: #667085;
  margin: 0px;
}
.WelcomeBtns button {
  height: 40px;
  padding: 0px 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 14px;
  border-radius: 8px;
  cursor: pointer;
}
.WelcomeOne {
  border: 1px solid #d0d5dd;
  background-color: #fff;
}
.WelcomeOne svg {
  height: 20px;
  width: 20px;
}
.WelcomeTwo {
  background-color: #101828;
  color: #fff;
  border: none;
}
.WelcomeTwo svg {
  height: 20px;
  width: 20px;
  color: #fff;
}

.addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  height: 40px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  border: none;
}
.addPropertyButton svg {
  height: 22px;
  width: 22px;
}

.main-div-modal {
  width: 100%;
  height: 500px;
  display: flex;
  flex-direction: column;
  border-radius: 10px !important;
  position: relative;
}

.close-div {
  height: 40px;
  width: 40px;
  display: flex;
  position: absolute;
  right: 0;
  top: 10px;
  align-items: center;
  justify-content: center;
  background-color: #cfc0c0;
  border-radius: 50px;
}
