.PDetailsMain .listing-container button {
  padding: 0px 26px;
  height: 44px;
  border-radius: 8px;
  background-color: #101828;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #fff;
  border: none;
  font-weight: 600;
  font-size: 14px;
}
.PMainImage {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  width: 90%;
}
.PMainImage .ImageOne {
  width: 60%;
}
.PMainImage .ImageOne img {
  height: 400px;
  width: 100%;
}
.ImageTwo {
  height: 400px;
}
.PImageText {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-block: 20px;
}
.PImageText p {
  font-weight: 400;
  font-size: 24px;
  color: #000000;
  margin-bottom: 0px;
}
.PImageText p span {
  font-weight: 500;
  font-size: 24px;
  color: #000000;
}
.PPriceFields {
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 85%;
}
.PInputBack {
  height: 48px;
  padding: 0px 10px;
  border-radius: 8px;
  border: 1px solid #d0d5dd;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px 4px 0px #00000040;
}
.PInputBack p {
  margin-bottom: 0px;
}
.PriceWidth {
  width: 50%;
  margin-top: 10px;
}
.PPriceFields span {
  color: #ff0000;
}
.PPButton {
  height: 60px;
  background-color: #f3f3f3;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 4px;
  border-radius: 10px;
}
.PPButton button {
  height: 47px;
  padding: 0px 20px;
  background-color: #fff;
  border-radius: 8px;
  font-weight: 500;
  font-size: 16px;
  color: #0f5dfa;
  border: none;
  border: 1px solid #00000021;
}
.PriceLess {
  width: 25%;
}

.Width100 {
  margin-top: 10px;

  height: 48px;
  width: 100%;
  padding: 0px 10px;
  border-radius: 8px;
  border: 1px solid #d0d5dd;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px 4px 0px #00000040;
}
.GetWidth {
  width: 50%;
}
.Width100 p {
  margin-bottom: 0px;
}
.LabelCLass {
  margin-top: 10px;
}
.TextAreaDiv {
  padding: 20px;
  display: flex;
  flex-direction: column;
  width: 90%;
}
.TextAreaDiv .TextArea {
  height: 190px;
  border-radius: 12px;
  border: 1px solid #999999;
  padding: 10px;
  overflow-y: scroll;
}
.TextAreaDiv label {
  font-weight: 500;
  font-size: 20px;
  color: #000000;
}
.RoomSFields {
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin-top: 10px;
}
.RoomChild {
  display: flex;
  align-items: center;
  width: 28%;
  justify-content: space-between;
}
.RoomChild p {
  margin-bottom: 0px;
}
.RoomChild button {
  border-radius: 12px;
  width: 220px;
  height: 52px;
  padding: 0px 10px;
  border: 1px solid #00000023;
  background-color: #fff;
  font-weight: 400;
  font-size: 16px;
}
.FlexWidth {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  width: 64%;
}
.FlexWidth .PInputBack {
  width: 80%;
}
.FlexWidth p {
  margin-bottom: 0px;
}
.RoomChild .CircularProgressbar {
  width: 25%;
  vertical-align: middle;
  height: 40px;
}
.RoomChild .CircularProgressbar .CircularProgressbar-path {
  stroke: #000 !important;
}
.RoomChild .CircularProgressbar .CircularProgressbar-text {
  fill: #000 !important;
  font-size: 35px !important;
}
.PropertyMain {
  width: 100%;
  padding: 20px;
}
.PropertyMain table {
  width: 90%;
}
.PropertyMain h2 {
  font-weight: 500;
  font-size: 20px;
  color: #101828;
}
.PropertyMain th {
  border-bottom: 1px solid #dbdade;
  padding: 10px;
}
.PropertyMain td {
  padding: 10px;
}
.AvailAble {
  color: #368b78;
}
.ViewDetail {
  color: #0f60ff;
}
.PropertyLastFlex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
}
