.PrivateMain {
  background-color: #fff;
  padding: 20px;
  border-radius: 18px;
  margin-top: 30px;
  width: 49%;
}
.PrivTable {
  display: flex;
  align-items: center;
  width: 100%;
  gap: 10px;
}
.PrivTable h4,
.PrivTable h3,
.PrivTable p {
  margin: 0px;
}
.PrivTable h4 {
  font-weight: 400;
  font-size: 16px;
  color: #292929;
}
.PrivateMain .Head {
  font-weight: 500;
  font-size: 20px;
  color: #292929;
  margin: 0px;
}
.PrivTable p {
  color: #888888;
  font-size: 11px;
  font-weight: 400;
}
.PrivTable p svg {
  height: 12px;
  width: 12px;
  color: black;
}
.PrivatableMain table {
  width: 100%;
  margin-top: 20px;
}
.PrivatableMain td,
.PrivatableMain th {
  text-align: left;
}
.PrivatableMain th {
  color: #9b9b9b;
  font-weight: 400;
  font-size: 14px;
}
.PrivatableMain td {
  padding: 10px 0px;
}
.PrivTableHeadings h3 {
  margin: 0px;
  font-size: 20px;
  font-weight: 600;
}
.RoomImages {
  height: 59px;
  width: 84px;
  border-radius: 10px;
}
/* .................................. */
.Verified {
  color: #0da000;
  font-size: 16px;
  font-weight: 400;
  margin: 0px;
}
.HoursAgo {
  font-weight: 400;
  font-size: 14px;
  color: #393939b2;
  margin: 0px;
}
.UserTableImg {
  height: 48px;
  width: 48px;
  border-radius: 50%;
}
.ViewAllButton {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ViewAllButton button {
  height: 42px;
  padding: 0px 20px;
  border: 0px;
  background-color: #101828;
  color: #ffffff;
  font-weight: 500;
  font-size: 16px;
  border-radius: 40px;
}
@media (max-width: 1012px) {
  .PrivTableHeadings h3 {
    font-size: 16px;
  }
  .PrivTable h4 {
    font-size: 14px;
  }
  .Verified {
    font-size: 14px;
  }
  .PrivatableMain td {
    font-size: 14px !important;
  }
  .PrivateMain .Head {
    font-size: 18px;
  }
  .ViewAllButton button {
    height: 31px;
    padding: 0px 14px;
    font-size: 14px;
  }
  .WelcomeBtns button {
    height: 35px !important;
    padding: 0px 6px !important;
    gap: 5px !important;
    font-size: 12px !important;
  }
  .Cardsmonth {
    gap: 0px;
  }
  .CardMAIN .Cardsss {
    padding: 15px;
  }
  .saidbar-main .setting {
    left: 20px;
  }
  .saidbar-main .setting i {
    font-size: 16px !important;
  }
}
@media (max-width: 982px) {
  .PrivTableHeadings h3 {
    font-size: 14px;
  }
  .saidbar-main .menu ul li a svg {
    margin-right: 8px !important;
  }
}
@media (max-width: 923px) {
  .WelcomeHead p {
    font-size: 11px !important;
  }
  .WelcomeHead h1 {
    font-size: 20px !important;
  }
  .PrivTable h4 {
    font-size: 12px;
  }
  .PrivTable p {
    font-size: 8px;
  }
  .RoomImages {
    height: 50px;
    width: 70px;
  }
  .HoursAgo {
    font-size: 10px !important;
  }
  .Verified {
    font-size: 10px !important;
  }
  .PrivatableMain td {
    font-size: 10px !important;
  }
  .PrivatableMain th {
    font-size: 12px !important;
  }
}
@media (max-width: 900px) {
  .GrayHome {
    padding: 20px !important;
    height: 100vh;
  }
  .hamburger svg {
    top: 25px;
  }
  .WelcomeHead {
    margin-left: 30px !important;
    gap: 4px !important;
  }
  .saidbar-main .LinkLogo {
    margin-top: 40px !important;
  }
  .saidbar-main .setting {
    display: flex;
    justify-content: start !important;
  }
}
@media (max-width: 734px) {
  .TablesFlex {
    display: flex;
    justify-content: space-between;
    flex-direction: column !important;
  }
  .PrivateMain {
    margin-top: 20px;
    width: 100% !important;
  }
  .PrivatableMain td {
    font-size: 14px !important;
  }
  .Verified {
    font-size: 14px !important;
  }
  .HoursAgo {
    font-size: 14px !important;
  }
  .CardMAIN .Cardsss {
    width: 48.5%;
    margin-top: 20px;
  }
  .GrayHome {
    height: auto !important;
  }
  .CardMAIN {
    margin-top: 0px !important;
    flex-wrap: wrap;
  }
}
@media (max-width: 650px) {
  .WelcomeHead h1 {
    font-size: 17px !important;
  }
  .WelcomeHead p {
    font-size: 10px !important;
  }
  .hamburger svg {
    left: 13px;
  }
  .WelcomeBtns button {
    height: 31px !important;
    padding: 0px 6px !important;
    gap: 5px !important;
    font-size: 10px !important;
  }
}
@media (max-width: 594px) {
  .WelcomeMain {
    align-items: start !important;
    flex-direction: column;
    gap: 10px;
  }
  .WelcomeBtns {
    margin-left: 30px;
  }
}
@media (max-width: 377px) {
  .GrayHome {
    padding: 20px 10px !important;
  }
  .hamburger svg {
    left: 5px;
  }
  .CardMAIN .Cardsss h4 {
    font-size: 14px;
  }
  .CardMAIN .Cardsss h2 {
    font-size: 18px;
  }
  .CardMAIN .Cardsmonth p {
    font-size: 10px;
  }
  .CardMAIN .Cardsss {
    height: 180px;
    margin-top: 10px;
  }
  .CardMAIN .Cardsmonth h6 {
    font-size: 10px;
  }
  .PrivateMain {
    padding: 10px;
  }
  .ViewAllButton button {
    height: 30px;
    padding: 0px 10px;
    font-size: 12px;
  }
  .PrivatableMain td {
    font-size: 12px !important;
    padding: 2px 0px;
  }
  .Verified {
    font-size: 12px !important;
  }
  .HoursAgo {
    font-size: 12px !important;
  }
  .PrivatableMain {
    width: 360px !important;
    overflow-x: scroll !important;
  }
  .PrivatableMain table {
    min-width: 420px !important;
    overflow-x: scroll !important;
  }
}
@media (max-width: 1411px) {
  .saidbar-main .setting {
    padding: 0px !important;
  }
}
