.inputAndButton {
  display: flex;
  align-items: center;
  gap: 10px;
}
.TaskMain {
  padding: 20px;
}
.inputAndButton .input {
  border: 1px solid #f7f7f7;
  padding: 2px 8px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.inputAndButton .input input {
  outline: none;
  border: none;
  background-color: transparent;
}
.inputAndButton .input input::placeholder {
  font-size: 12px;
}
.TaskHead h2 {
  font-weight: 500;
  font-size: 28px;
  color: #101828;
}
.TaskHead p {
  font-weight: 500;
  color: #8e8e93;
  font-size: 16px;
}
.TaskMain .ReportTable tbody tr {
  border-top: 10px solid #f7f7f7;
  border-right: 0px solid #f7f7f7 !important;
  border-left: 0px solid #f7f7f7 !important;
  border-radius: 10px;
}
.TaskMain .ReportTable table th {
  padding: 10px !important;
  font-weight: 400;
  font-size: 18px;
  color: #101828;
  background-color: #f7f7f7;
}
.Mini-Main-Btns1 {
  font-size: 14px;
}
.Mini-Main-Btns2 {
  font-size: 14px;
}
.TaskMain .dropdown-item {
  padding: 0px 5px !important;
}
.TaskMain .Mini-Main-Btns1 svg {
  color: #101828 !important;
}
.HotelTask {
  display: flex;
  align-items: center;
  gap: 5px;
}
.HotelTask img {
  height: 46px;
  width: 74px;
  border-radius: 4px;
}
.HotelTask h3 {
  font-weight: 500;
  font-size: 16px;
  color: #101828;
  margin: 0px;
}
.HotelTask p {
  font-weight: 400;
  font-size: 14px;
  color: #101828;
  margin: 0px;
}
.BrokerNameee {
  display: flex;
  align-items: center;
  gap: 5px;
}
.BrokerNameee img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}
.BrokerNameee p {
  font-weight: 500;
  font-size: 12px;
  color: #555555;
  margin: 0px;
}
.HotelTask .ReportTable tbody td {
  padding: 8px !important;
}
.Important {
  font-weight: 600;
  font-size: 12px;
  color: #0f5dfa;
  margin: 0px;
}
.TaskTabs table select {
  height: 25px;
  border-radius: 20px;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
  outline: none;
  border: none;
}
.TaskTabs table td {
  font-weight: 500;
  font-size: 12px;
  color: #555555;
}
.NotImportant {
  color: black;
  font-weight: 600;
  margin: 0px;
  font-size: 12px;
}
