.Tables {
  margin-top: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}
.Tables .mainTables {
  width: 98%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Tables .mainTables .topMainTabs {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Tables .mainTables .tabsContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f7f7f7;
  padding: 20px 0px;
  margin: 20px 0px;
}
.Tables .mainTables .tabsContainer .inputAndButton,
.Tables .mainTables .tabsContainer .tabs {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.Tables .mainTables .tabsContainer .tabs .tabButton {
  background-color: #fff;
  color: #101828;
  border: none;
  border-radius: 7px;
  padding: 7px 14px;
  font-size: 18px;
  font-weight: 600;
}
.Tables .mainTables .tabsContainer .tabs .soldTab {
  color: #ffcd03;
}

.Tables .mainTables .tabsContainer .tabs .activeTab {
  color: #000dff;
}

.Tables .mainTables .tabsContainer .tabs .pendingTab {
  color: #ff0000;
}

.Tables .mainTables .tabsContainer .tabs .active {
  background-color: #101828;
  color: #fff;
}
.Tables .mainTables .tabsContainer .inputAndButton .input {
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 3px 10px;
  gap: 3px;
  background-color: #fff;
  border-radius: 7px;
}
.Tables .mainTables .tabsContainer .inputAndButton .input svg {
  width: 20px;
  height: 20px;
  color: #8e8b8b;
}
.Tables .mainTables .tabsContainer .inputAndButton .input input {
  padding: 7px 11px;
  border: none;
  outline: none;
  color: #8e8b8b;
  font-size: 12px;
  font-weight: 400;
}
.Tables .mainTables .tabsContainer .inputAndButton .addPropertyButton {
  background-color: #101828;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 7px;
  padding: 5px 17px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
}

.Tables .mainTables .topMainTabs .tabelMain {
  width: 100%;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain h3 {
  font-size: 18px;
  font-weight: 600;
  color: #2e2a2a;
  background-color: #0f5dfa26;
  border-radius: 10px;
  margin: 0px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Action {
  width: 97px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Location {
  width: 196px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Status {
  width: 178px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Type {
  width: 164px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Price {
  width: 83px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .Property {
  width: 367px;
  height: 40px;
}
.Tables .mainTables .topMainTabs .tabelMain .topMain .ID {
  width: 104px;
  height: 40px;
}

.Tables .mainTables .topMainTabs .tabelMain .topMain .Action,
