}
.Tables .mainTables .topMainTabs .tabelMain .tabContent .All .Action span {
  cursor: pointer;
  width: 48%;
  height: 100%;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.3px solid #929aa9;
  margin: 0px;
  color: #333333;
}

.Tables .mainTables .topMainTabs .tabelMain .tabContent .All .Action span svg {
