  font-weight: 600;
}
/* Modal CSS */
.Tables .modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.Tables .modalContent {
  position: relative;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  text-align: center;
  border-radius: 44px;
  width: 648px;
}
.Tables .modalContent span {
  position: absolute;
  right: -5px;
  top: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px #00000040;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  cursor: pointer;
}
.Tables .modalContent img {
  width: 40%;
  margin-bottom: 2rem;
}

.Tables .modalContent h4 {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
  color: #212121;
}
.Tables .modalContent p {
  color: #212121;
  font-size: 16px;
  font-weight: 400;
}
.Tables .modalContent .btns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
}

.Tables .modalContent .btns button {
  width: 47%;
  height: 58px;
  border-radius: 16px;
  color: 16px;
  font-weight: 700;
  background-color: #1018281a;
  border: none;
}
.Tables .modalContent .btns .yesBtn {
  background-color: #101828;
  color: #ffffff;
}
.All .Status {
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
  text-align: center;
}

.All .activeStatus {
  border: 0.3px solid #0f5dfa !important;
}
.All .activeStatus h5 {
  color: #0f5dfa !important;
}

.All .pendingStatus {
  border: 0.3px solid #ff0000 !important;
}
.All .pendingStatus h5 {
  color: #ff0000 !important;
}

.All .soldStatus {
  border: 0.3px solid #ffcd03 !important;
}
.All .soldStatus h5 {
  color: #ffcd03 !important;
}
