.IntegrationMain {
  padding: 20px;
}
.IntegrationMain h3 {
  font-size: 30px;
  font-weight: 500;
  color: #101828;
}
.IntegratTopic {
  margin-top: 30px;
  border-bottom: 1px solid #eaecf0;
}
.IntegratTopic h4 {
  font-size: 18px;
  font-weight: 500;
  color: #101828;
  margin: 0px;
}
.IntegratTopic p {
  font-weight: 400;
  font-size: 14px;
  color: #667085;
}
.IntegraFour {
  width: 40%;
  border: 1px solid #00000017;
  height: 330px;
  border-radius: 30px;
  padding: 30px;
}
.IntegraFour:hover {
  box-shadow: 0px 4px 4px 0px #00000040;
}
.IntegraCards {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}
.InterIMG {
  display: flex;
  justify-content: space-between;
}
.InterIMG img {
  height: 90px;
  width: 90px;
}
.InterIMG svg {
  width: 24px;
  height: 24px;
  color: #101828;
}
.IntegraFour h4 {
  font-weight: 700;
  font-size: 26px;
  color: #101828;
  margin-top: 20px;
}
.IntegraFour p {
  color: #101828;
  font-weight: 400;
  font-size: 16px;
}
.InterConnect {
  display: flex;
  align-items: center;
  gap: 20px;
  background-color: #fff;
  height: 50px;
  padding: 0px 10px;
  border-radius: 10px;
}
.InterConnect h5 {
  font-weight: 400;
  font-size: 16px;
  color: #000000;
  margin: 0px;
}
.InterSwitch {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}
.InterSwitch .form-switch .form-check-input {
  height: 31px;
  width: 51px;
  box-shadow: none !important;
  border: 1px solid #c3c3c3 !important;
}
.InterSwitch .form-check-input:checked {
  background-color: #000000 !important;
  border-color: #000000 !important;
}
.InterConnect svg {
  color: #368b78;
  height: 20px;
  width: 20px;
}
.InterCancel svg {
  color: #ff3b30;
  height: 20px;
  width: 20px;
}
